import { authAPI } from '../api/api'
import { stopSubmit } from 'redux-form'

const SET_USER_DATA = 'samurai-network/auth/SET_USER_DATA'
const SET_CAPTCHA_SUCCESS = 'samurai-network/auth/SET_CAPTCHA_SUCCESS'

let initialState = {
	userId: null,
	email: null,
	login: null,
	isAuth: false,
	url: null,
}

const authReducer = (state = initialState, action) => {
	switch (action.type) {
		case SET_USER_DATA:
		case SET_CAPTCHA_SUCCESS:
			return {
				...state,
				...action.payload,
			}

		default:
			return state
	}
}

export const setAuthUserData = (userId, email, login, isAuth, captcha) => ({
	type: SET_USER_DATA,
	payload: { userId, email, login, isAuth, url: captcha },
})
export const setCaptha = url => ({
	type: SET_CAPTCHA_SUCCESS,
	payload: { url },
})

export const getAuthUserData = () => async dispatch => {
	let response = await authAPI.me()

	if (response.data.resultCode === 0) {
		let { id, login, email } = response.data.data
		dispatch(setAuthUserData(id, email, login, true))
	}
}
export const getCaptcha = () => async dispatch => {
	let response = await authAPI.getCaptcha()

	let url = response.data.url
	dispatch(setCaptha(url))
}

export const login =
	(email, password, rememberMe, captcha = null) =>
	async dispatch => {
		let response = await authAPI.login(email, password, rememberMe, captcha)
		if (response.data.resultCode === 0) {
			dispatch(getAuthUserData())
		} else {
			if (response.data.resultCode === 10) {
				dispatch(getCaptcha())
			}
			let message =
				response.data.messages.length > 0
					? response.data.messages[0]
					: 'Some error'
			dispatch(stopSubmit('login', { _error: message }))
		}
	}

export const logout = () => async dispatch => {
	let response = await authAPI.logout()

	if (response.data.resultCode === 0) {
		dispatch(setAuthUserData(null, null, null, false, null))
	}
}

export default authReducer
